import React from "react";
import Modal from "@material-ui/core/Modal";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import { Link } from "react-router-dom";
import SimpleSnackbar1 from "../Snackbar/Snackbar1";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

const useStyles = makeStyles(() => ({
  move: {
    color: "#F03636",
    fontSize: "16px",
    marginLeft: " 50px",
    marginRight: "14px",
  },

  noBtn: {
    width: "50%",
    border: "1px solid #707070",
    textTransform: "none",
    "&:hover": {
      color: "grey",
    },
  },

  yesBtn: {
    width: "50%",
    border: "1px solid #707070",
    textTransform: "none",
    "&:hover": {
      color: "grey",
    },
  },

  modal: {
    border: "1px",
    borderRadius: 15,
    backgroundColor: "white",
    width: 300,
    margin: "auto",
    marginTop: 230,
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center",
    outline: "none",
  },

  addjdBtn: {
    textTransform: "none",
    backgroundColor: "#04724D",
    marginTop: 10,
    marginBottom: 10,
    width: 100,
    float: "right",
    marginRight: 10,
  },
}));

export default function ConfirmationModel(props) {
  const [open1, setOpen] = React.useState(false);
  const classes = useStyles();
  const { confirmationText, confirmBtnText, cancelBtnText, handleClose, open } =
    props;
  const handleClose1 = () => {
    setOpen(true);
  };

  return (
    <div>
      <Modal open={open} onClose={handleClose}>
        <div className={classes.modal}>
          <p style={{ paddingTop: "20px", paddingBottom: "20px" }}>
            {confirmationText}
          </p>

          <Button onClick={handleClose} className={classes.noBtn}>
            <Link
              to="/jddata"
              className={classes.link}
              style={{ color: "#04724D", fontSize: "16px" }}
            >
              {cancelBtnText}
            </Link>
          </Button>
          <Button className={classes.yesBtn} onClick={handleClose1}>
            <Link
              className={classes.link}
              to="/jd"
              style={{ color: "#04724D", fontSize: "16px" }}
            >
              {confirmBtnText}
            </Link>
          </Button>
        </div>
      </Modal>
    </div>
  );
}
