import React from "react";
import Button from "../../components/Controls/Button/Button";
import ConfirmationModel from "../../components/confirmation/confimation";
import { connect } from "react-redux";
import * as Actions1 from "../../store/Feature1/FeatureAction";
import * as Actions from "../../store/Candidate/CandidateAction";
import * as Actions2 from "../../store/JobDescription/JobDescriptionAction";
import * as CActions from "../../store/Resumesource/resumesourceAction";
import { bindActionCreators } from "redux";
import TableComponent from "../../components/Controls/Table/sortableTable";
import SearchIcon from "../../assets/images/schedule/search-icon.svg";
import { Card, CardContent } from "@material-ui/core";
import CheckIcon1 from "../../assets/images/checkicon1.svg";
import CheckIcon2 from "../../assets/images/Icon ionic-md-checkmark.svg";
import ClearIcon1 from "../../assets/images/clearIcon1.svg";
import Modal from "@material-ui/core/Modal";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import Box from "@material-ui/core/Box";
import ChatIcon1 from "../../assets/images/Icon material-message.svg";
import DeleteIcon from "@material-ui/icons/Delete";
import ReactPlayer from "react-player";
import SelectInput from "../../components/Atoms/SelectInput/SelectInput";
import "./Followup.css";
import Sendmessage from "../../components/Atoms/Sendmessage";
import SearchInput from "../../components/Atoms/CustomizedInputs/SearchInput";
import Button1 from "@material-ui/core/Button";
import { Link } from "react-router-dom";
import Checkbox from "@material-ui/core/Checkbox";
import RadioButtonUncheckedIcon from "@material-ui/icons/RadioButtonUnchecked";
import RadioButtonCheckedIcon from "@material-ui/icons/RadioButtonChecked";
import sampleResume from "../../assets/images/sampleResume.jpg";
import resumeDownloadIcon from "../../assets/images/resumeDownloadIcon.svg";
import messageIcon from "../../assets/images/Icon feather-check-circle.svg";
import ClearIcon2 from "../../assets/images/clearIcon2.svg";
import axios from "axios";
import serverAddress from "../../config";
import { withRouter } from "react-router-dom";
import ChatIcon3 from "@material-ui/icons/Chat";
import CheckIcon from "@material-ui/icons/Check";
import ClearIcon from "@material-ui/icons/Clear";
import { debounce } from "lodash";
import * as Actions5 from "../../store/Sendmessage/SendAction";
import Video1 from "../JD/v1.mp4";
import Video2 from "../JD/v2.mp4";
import Video3 from "../JD/v3.mp4";
import Video4 from "../JD/v4.mp4";
import Video5 from "../JD/v5.mp4";
import Video6 from "../JD/v6.mp4";
import RichTextEditor from "react-rte";
import ReactGA from "react-ga";
const dropdonVal = JSON.parse(localStorage.getItem("dropdown"));
class FollowUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      msgBody: RichTextEditor.createEmptyValue(),
      open1: false,
      setOpen1: false,
      openMessage: false,
      job: "",
      jd_id: "",
      options: [],
      page: 1,
      limit: 5,
      value: "",
      id: dropdonVal && dropdonVal.value,
      searchItem: "",
      name: dropdonVal && dropdonVal.label,
      openRes: "",
      opensuccessmodal: false,
      checked: false,
      crossed: false,
      isShowDeleteModal: false,
      candidate_details: [],
      setEmail: [],
      videoInterview: Video1,
      videoInterviewQuestion1: "Who are your role models and mentors, and why?",
      videoInterviewQuestion2: "",
      videoInterviewQuestion3: "",
      videoInterviewQuestion4: "",
      videoInterviewQuestion5: "",
      videoInterviewQuestion6: "",
      q1: true,
      q2: false,
      q3: false,
      q4: false,
      q5: false,
      q6: false,
    };
  }

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    const payload = {
      id: this.state.id,
    };
    this.props.resumesourceAction.getCandidateById(payload);
    this.props.JobDescriptionAction.getJobDescriptionData();
  }
  componentDidUpdate(prevProps) {
    const prev = prevProps && prevProps.resumesourceReducer;
    const cur = this.props && this.props.resumesourceReducer;
    if (prev.isCandidateUpdated !== cur.isCandidateUpdated) {
      const payload = {
        id: this.state.id,
      };
      this.props.resumesourceAction.getCandidateById(payload);
    }
  }
  handleCloseDeleteModal = () => {
    this.setState({ isShowDeleteModal: false });
  };

  handleopen = () => {
    this.setState({ open1: true });
  };
  handleSendMessage = () => {
    this.setState({ openMessage: true });
  };
  handleOpenMessage = (val) => {
    if (val) {
      let obj = {
        id: val.original.id,
        email: val.original.email,
        name: val.original.name,
      };

      let arr = [];
      arr = this.state.setEmail;
      arr.push(obj);
      this.setState({ setEmail: arr });
    }
  };
  handleBodyData = (e) => {
    this.setState({ msgBody: e });
  };
  handleCloseMessage = () => {
    this.setState({ openMessage: false });
  };

  handleClose = () => {
    this.setState({ open1: false });
  };

  handleDeleteCandidate = (val) => {
    const payload = {
      isDeleted: "true",
      id: val.original.id,
    };
    this.props.resumesourceAction.updateCandidate(payload);
  };

  handleSelect = (e) => {
    const payload1 = {
      value: e.value,
      label: e.label,
    };
    localStorage.setItem("dropdown", JSON.stringify(payload1));
    this.setState({ id: e.value, name: e.label });
    const payload = {
      id: e.value,
    };
    this.getdata(payload);
  };

  handlePagination = async (e) => {
    await this.setState({ page: e.selected + 1 });

    const payload = {
      page: this.state.page,
      limit: this.state.limit,
    };
    this.props.CandidateAction.getJobDescriptionData(payload);
  };
  handleMailFiles = (files) => {
    this.setState({
      mailFile: files.base64,
    });
  };

  handleSearch = (e) => {
    this.setState({ value: e.value });
    this.setState({ searchItem: e.target.value });
    this.debounceLog(e.target.value);
  };

  debounceLog = debounce((val) => {
    this.setState({ showLoader: true });
    const payload = {
      name: val,
      id: this.state.jd_id,
      isDeleted: false,
    };
    this.props.resumesourceAction.searchCandidateData(payload);
  }, 700);

  handleOpenResume = () => {
    this.setState({ openRes: true });
  };

  handleCloseResume = () => {
    this.setState({ openRes: false });
  };
  handleCloseModal = () => {
    this.setState({ opensuccessmodal: false });
  };

  toggleCheck = (val) => {
    const currentCheckState = this.state.checked;

    if (this.state.crossed == false) {
      this.setState({ checked: !currentCheckState });
    }
  };

  toggleCross = () => {
    const currentCrossState = this.state.crossed;
    if (this.state.checked == false) {
      this.setState({ crossed: !currentCrossState });
    }
  };

  handleMailSend = () => {
    const payload = {
      to: this.state.setEmail[0].email,
      subject: "Welcome to CronJ IT Technology Pvt Ltd!!!",
      body: this.state.msgBody,
      // filename: "hjdfhjd.pdf",
    };
    this.props.SendAction.addSendData(payload);
  };
  async getdata(payload) {
    this.props.resumesourceAction.getCandidateById(payload);
  }

  handleNextVideo1 = () => {
    this.setState({
      videoInterview: Video1,
      videoInterviewQuestion1: "Who are your role models and mentors, and why?",
      q1: true,
      q2: false,
      q3: false,
      q4: false,
      q5: false,
      q6: false,
      // videoInterview: "http://localhost:3000/assets/videos/video1.mp4",
    });
  };

  handleNextVideo2 = () => {
    this.setState({
      videoInterview: Video2,
      videoInterviewQuestion2: "Which season do you like the most and why?",
      q2: true,
      q1: false,
      q3: false,
      q4: false,
      q5: false,
      q6: false,
      // videoInterview: "https://www.youtube.com/watch?v=Tuw8hxrFBH8",
    });
  };
  handleNextVideo3 = () => {
    this.setState({
      videoInterview: Video3,
      videoInterviewQuestion3: "Explain how the year 2020 was for you?",
      q3: true,
      q1: false,
      q2: false,
      q4: false,
      q5: false,
      q6: false,
      // videoInterview: "https://www.youtube.com/watch?v=HvQ1WrN6AP4",
    });
  };
  handleNextVideo4 = () => {
    this.setState({
      videoInterview: Video4,
      videoInterviewQuestion4:
        "What is the trickiest technical question you have solved? What was your approach and how much time did it take you to solve it?",
      q4: true,
      q1: false,
      q2: false,
      q3: false,
      q5: false,
      q6: false,
      // videoInterview: "https://www.youtube.com/watch?v=_9BcM2de4gE",
    });
  };
  handleNextVideo5 = () => {
    this.setState({
      videoInterview: Video5,
      videoInterviewQuestion5:
        "Explain at least 2 OOPS concepts with simple example.",
      q5: true,
      q1: false,
      q2: false,
      q3: false,
      q4: false,
      q6: false,
      // videoInterview: "https://www.youtube.com/watch?v=lkDBImBAmN0",
    });
  };
  handleNextVideo6 = () => {
    this.setState({
      videoInterview: Video6,
      videoInterviewQuestion6: "Explain Big O notations with a simple example.",
      q6: true,
      q1: false,
      q2: false,
      q3: false,
      q4: false,
      q5: false,
      // videoInterview: "https://www.youtube.com/watch?v=VSceuiPBpxY",
    });
  };
  handleNextVideo7 = () => {
    this.setState({
      // videoInterview: "https://www.youtube.com/watch?v=TLKxdTmk-zc",
    });
  };
  handleNextVideo8 = () => {
    this.setState({
      // videoInterview: "https://www.youtube.com/watch?v=La9oLLoI5Rc",
    });
  };
  handleNextVideo9 = () => {
    this.setState({
      // videoInterview: "https://www.youtube.com/watch?v=uT_GcOGEFsk",
    });
  };
  handleNextVideo10 = () => {
    this.setState({
      // videoInterview: "https://www.youtube.com/watch?v=a-3CB4_a33o",
    });
  };

  render() {
    let searchData = [];
    let data = [];
    let data2 = [];
    let dropDowndata = [];
    let totalPage = 1;
    let optionsdata = [];
    let dataLength = 0;
    let data2Length = 0;
    if (
      this.props &&
      this.props.JobDescriptionReducer &&
      this.props.JobDescriptionReducer.getJobDescriptionData &&
      this.props.JobDescriptionReducer.getJobDescriptionData.data &&
      this.props.JobDescriptionReducer.getJobDescriptionData.data
        .jobDescriptions
    ) {
      dropDowndata =
        this.props.JobDescriptionReducer.getJobDescriptionData.data
          .jobDescriptions;
      optionsdata = dropDowndata.map((d) => ({
        value: d._id,
        label:
          " " +
          d.title.jobtitle +
          " - " +
          d.minExperience +
          " to " +
          d.maxExperience +
          "Years of experience",
      }));
    }
    if (
      this.props &&
      this.props.resumesourceReducer &&
      this.props.resumesourceReducer.getCandidateData &&
      this.props.resumesourceReducer.getCandidateData.data &&
      this.props.resumesourceReducer.getCandidateData.data.users
    ) {
      let data1 = [];
      this.props.resumesourceReducer.getCandidateData.data.users.map((item) => {
        data1.push({
          id: item._id,
          name: item.name,
          email: item.email,
          phone: item.phone,
        });
      });
      searchData = data1;
    }

    if (
      this.props &&
      this.props.resumesourceReducer &&
      this.props.resumesourceReducer.getCandidateByIdData &&
      this.props.resumesourceReducer.getCandidateByIdData.data &&
      this.props.resumesourceReducer.getCandidateByIdData.data.resumesource
    ) {
      let data1 = [];
      let data2 = [];
      this.props.resumesourceReducer.getCandidateByIdData.data.resumesource.map(
        (item) => {
          if (item.status === "new") {
            data1.push({
              id: item._id,
              name: item.name,
              email: item.email,
              phone: item.phone,
            });
          } else if (item.status === "response") {
            data2.push({
              id: item._id,
              name: item.name,
              email: item.email,
              phone: item.phone,
            });
          }
        }
      );
      dataLength = data1.length;
      data = data1;
      data2Length = data2.length;
    }

    let inboundDatacolumn = [
      {
        Header: "Name",
        accessor: "name",
        minWidth: 150,
        style: { textAlign: "left" },
      },
      {
        Header: "Email",
        accessor: "email",
        minWidth: 200,

        style: { textAlign: "left" },
      },
      {
        Header: "Phone",
        accessor: "phone",
      },

      {
        Header: "Resume",

        className: "resumeCell",
        Cell: (props) => (
          <div>
            {
              <Button className='view-icon-1' onClick={this.handleOpenResume}>
                View <ChevronRightIcon />
              </Button>
            }
          </div>
        ),
      },
      {
        Header: "Interview",

        className: "interviewCell",

        Cell: (props) => (
          <div>
            {
              <span>
                <Button onClick={this.handleopen} className='interview-icon-1'>
                  Open
                  <ChevronRightIcon />
                </Button>
              </span>
            }
          </div>
        ),
      },

      {
        Header: "",
        accessor: "message",
        maxWidth: 70,
        Cell: (val) => (
          <div>
            <img
              src={ChatIcon1}
              onClick={(e) => this.handleOpenMessage(e, val)}
              className='chat-icon-1'
            ></img>
          </div>
        ),
        style: {
          borderColor: "transparent",
        },
      },
      {
        Header: "",
        accessor: "delete",
        maxWidth: 60,
        Cell: (val) => (
          <div>
            <DeleteIcon
              onClick={() => this.handleDeleteCandidate(val)}
              className='delete-icon-1'
            />
          </div>
        ),
        style: {
          borderColor: "transparent",
        },
      },
      {
        Header: "",
        accessor: "radio",
        maxWidth: 60,
        Cell: (props) => (
          <div>
            <Checkbox
              className='checkbox-size'
              icon={
                <RadioButtonUncheckedIcon className='select-unchecked-icon' />
              }
              checkedIcon={<RadioButtonCheckedIcon className='select-icon' />}
            />
          </div>
        ),
        style: {
          borderColor: "transparent",
        },
      },
    ];

    return (
      <div>
        <Card className='card1'>
          <div style={{ paddingBottom: "20px" }}>
            <div className='dropdown-10'>
              <div className='fl-DropdownStyle'>
                <SelectInput
                  className='react-select project'
                  onChange={this.handleSelect}
                  options={optionsdata}
                  value={{
                    value: this.state.id,
                    label: this.state.name,
                  }}
                  placeholder={
                    this.state.job ? this.state.job : "Select Job Description"
                  }
                />
              </div>
            </div>

            <div className='fl-searchInput'>
              <SearchInput
                className='search-input-main'
                imageOnClick={() => {}}
                placeholder='Search For Candidate'
                value={this.state.value}
                onChange={(e) => {
                  this.handleSearch(e);
                }}
                inputIcon={SearchIcon}
              />
            </div>
          </div>
        </Card>

        <Card className='card2'>
          <div className='card-class-all'>
            <Card className='card-style-1'>
              <div class='flex-1'>
                <div>
                  <p className='number-styling-1'> {dataLength}</p>
                  <p className='para-style-2'>invitation send</p>
                </div>
                <div>
                  <img src={CheckIcon1} className='check-icon-style-1'></img>
                </div>
              </div>
            </Card>

            <Card className='card-style-2'>
              <div class='flex-1'>
                <div>
                  <p className='number-styling-2'> {data2Length}</p>
                  <p className='para-style-2-2'>Interview Taken</p>
                </div>
                <div className='clear-icon-10'>
                  <img src={ClearIcon1} className='check-icon-style-2'></img>
                </div>
              </div>
            </Card>
          </div>
          <Modal
            className='user-modal'
            open={this.state.isShowDeleteModal}
            onClose={this.handleCloseDeleteModal}
          >
            <div className='user-delete'>
              <p>Do you want to delete this user?</p>

              <Button1
                className='user-button-delete'
                onClick={this.handleCloseDeleteModal}
              >
                <Link>No</Link>
              </Button1>

              <Button1
                className='user-button-delete'
                onClick={this.handleDelete}
              >
                <Link className={this.handleDelete}>Yes</Link>
              </Button1>
            </div>
          </Modal>
          <Modal open={this.state.open1} onClose={this.handleClose}>
            <Card className='video-modal-card'>
              <CardContent className='video-modal'>
                <div>
                  <div class='flex-3'>
                    <div>
                      <p className='question'>
                        <strong>Q</strong>:{" "}
                        {this.state.q1 === true
                          ? this.state.videoInterviewQuestion1
                          : ""}
                        {this.state.q2 === true
                          ? this.state.videoInterviewQuestion2
                          : ""}
                        {this.state.q3 === true
                          ? this.state.videoInterviewQuestion3
                          : ""}
                        {this.state.q4 === true
                          ? this.state.videoInterviewQuestion4
                          : ""}
                        {this.state.q5 === true
                          ? this.state.videoInterviewQuestion5
                          : ""}
                        {this.state.q6 === true
                          ? this.state.videoInterviewQuestion6
                          : ""}
                      </p>
                      <br />
                    </div>
                    <div className='clear-icon-12'>
                      <img src={ClearIcon2} onClick={this.handleClose}></img>
                    </div>
                  </div>

                  <div className='flex-2'>
                    <div className='react-player-responsiveness'>
                      <div class='embed-responsive embed-responsive-16by9'>
                        <video
                          controls
                          autostart
                          autoPlay
                          src={this.state.videoInterview}
                          type='video/mp4'
                        />
                      </div>
                    </div>
                    <CardContent className='card-content-20'>
                      <p className='video-modal-comment-styling'>Comments</p>
                      <textarea className='comment-card'>
                        Didn't understand the interview process
                      </textarea>
                    </CardContent>
                  </div>
                  <div className='para-position'>
                    <p>Answers</p>
                  </div>
                  <div className='pagination-1'>
                    <a className='page' onClick={this.a} href='#'>
                      1
                    </a>
                    <a
                      className='page'
                      onClick={this.handleNextVideo2}
                      href='#'
                    >
                      2
                    </a>
                    <a
                      className='page'
                      onClick={this.handleNextVideo3}
                      href='#'
                    >
                      3
                    </a>
                    <a
                      className='page'
                      onClick={this.handleNextVideo4}
                      href='#'
                    >
                      4
                    </a>
                    <a
                      className='page'
                      onClick={this.handleNextVideo5}
                      href='#'
                    >
                      5
                    </a>
                    <a
                      className='page'
                      onClick={this.handleNextVideo6}
                      href='#'
                    >
                      6
                    </a>
                    <a
                      className='page'
                      onClick={this.handleNextVideo7}
                      href='#'
                    >
                      7
                    </a>
                    <a
                      className='page'
                      onClick={this.handleNextVideo8}
                      href='#'
                    >
                      8
                    </a>
                    <a
                      className='page'
                      onClick={this.handleNextVideo9}
                      href='#'
                    >
                      9
                    </a>
                    <a
                      className='page'
                      onClick={this.handleNextVideo10}
                      href='#'
                    >
                      10
                    </a>
                    <a className='page' href='#'>
                      11
                    </a>
                  </div>
                </div>
              </CardContent>
            </Card>
          </Modal>

          {/* resume modal */}
          <Modal open={this.state.openRes} onClose={this.handleCloseResume}>
            <Card className='resume-modal-card'>
              <CardContent className='video-modal'>
                <div>
                  <object
                    width='100%'
                    height='700'
                    data='http://www.africau.edu/images/default/sample.pdf'
                    type='application/pdf'
                  ></object>
                </div>
              </CardContent>
            </Card>
          </Modal>

          <Modal
            open={this.state.opensuccessmodal}
            onClose={this.handleCloseModal}
          >
            <Card className='success-modal-card'>
              <div>
                <img className='img-position' src={messageIcon} alt=''></img>
                <div className='flex-4'>
                  <div className='content-props'>
                    <strong>Message send Successfully</strong>
                  </div>
                </div>
              </div>
            </Card>
          </Modal>

          <div className='fl-table-wrap'>
            <TableComponent
              data={this.state.searchItem ? searchData : data}
              columns={inboundDatacolumn}
              sortable={true}
            />
          </div>
          <div>
            {this.state.openMessage && (
              <Sendmessage
                open={this.state.openMessage}
                onClose={this.handleCloseMessage}
                msgBody={this.state.msgBody}
                setEmailMsg={this.state.setEmail}
                onBodyUpdate={this.handleBodyData}
                onMailSend={this.handleMailSend}
                onMailFile={this.handleMailFiles}
              />
            )}
            <Button
              onClick={(val) => this.handleSendMessage(val)}
              className='buttonModal'
            >
              SendMessage
            </Button>
          </div>
        </Card>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    FeatureReducer: state.FeatureReducer,
    JobDescriptionReducer: state.JobDescriptionReducer,
    resumesourceReducer: state.resumesourceReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    CandidateAction: bindActionCreators(Actions, dispatch),
    FeatureAction: bindActionCreators(Actions1, dispatch),
    JobDescriptionAction: bindActionCreators(Actions2, dispatch),
    resumesourceAction: bindActionCreators(CActions, dispatch),
    SendAction: bindActionCreators(Actions5, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(FollowUp));
